<template>
  <div id="roles">
    <!--  -->
    <Crumbs :crumbs="this.crumbs"></Crumbs>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="RolesDialogVisible = true"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="RolesDialogVisible"
        width="50%"
        @close="handleCloseRolse"
      >
        <!--  -->
        <el-form
          :model="RolesRuleForm"
          :rules="RolesRules"
          ref="RolesRuleRefs"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="roleName">
            <el-input v-model="RolesRuleForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="RolesRuleForm.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="RolesDialogVisible = false"> </el-button>
          <el-button type="primary" @click="handleAddRoles"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-table :data="RolesList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              v-for="(item1,index1) in scope.row.children"
              :key="item1.id"
              :class="['bdbottom',index1 === 0 ? 'bdtop' : '','vcenter']"
            >
              <!--  -->
              <el-col :span="6">
                <el-tag closable @close="removeById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="18">
                <el-row
                  v-for="(item2,index2) in item1.children"
                  :key="item2.id"
                  :class="[index2 === 0 ? '' : 'bdtop','vcenter']"
                >
                  <!--  -->
                  <el-col :span="6">
                    <el-tag
                      closable
                      @close="removeById(scope.row,item2.id)"
                      type="success"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag
                      closable
                      @close="removeById(scope.row,item3.id)"
                      type="warning"
                      v-for="(item3) in item2.children"
                      :key="item3.id"
                    >{{item3.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="handleEdit(scope.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="handleDeleteRoles(scope.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="handlePower(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="editDialogVisible"
        width="50%"
        @close="handleCloseEdit"
      >
        <!--  -->
        <el-form
          :model="RolesRuleFormEdit"
          :rules="RolesRules"
          ref="RolesRuleRefsEdit"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="roleName">
            <el-input v-model="RolesRuleFormEdit.roleName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="RolesRuleFormEdit.roleDesc"></el-input>
          </el-form-item>
        </el-form>
        <!--  -->
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="AddRoles"> </el-button>
        </span>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        :visible.sync="dialogVisiblePower"
        width="50%"
        @close="handleClosePower"
      >
        <!--  -->
        <el-tree
          ref="treeRefs"
          :default-checked-keys="keysList"
          :data="treeList"
          :props="defaultProps"
          show-checkbox
          node-key="id"
          default-expand-all
        ></el-tree>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisiblePower = false"> </el-button>
          <el-button type="primary" @click="handleAddPower"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import Crumbs from '../crumbs.vue'
import loginVue from '../../pages/login/login.vue'
export default {
  data() {
    return {
      crumbs: [
        {
          name: '',
          id: 0
        },
        {
          name: '',
          id: 1
        }
      ],
      // 
      RolesList: [],
      // 
      RolesDialogVisible: false,
      // 
      RolesRuleForm: {
        roleName: '',
        roleDesc: ''
      },
      // 
      RolesRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 2,
            max: 6,
            message: ' 2  6 ',
            trigger: 'blur'
          }
        ],
        roleDesc: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 2,
            max: 6,
            message: ' 2  6 ',
            trigger: 'blur'
          }
        ]
      },
      //  
      editDialogVisible: false,
      // 
      RolesRuleFormEdit: {
        roleName: '',
        roleDesc: ''
      },
      // 
      dialogVisiblePower: false,
      // 
      treeList: [],
      // 
      defaultProps: {
        children: 'children',
        label: 'authName'
      },
      // 
      keysList: [],
      // id
      roleId: ''
    }
  },
  components: {
    Crumbs
  },
  created() {
    this.getRolesList()
  },
  methods: {
    // 
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.RolesList = res.data
      this.$message.success('')
    },
    // 
    handleCloseRolse() {
      this.$refs.RolesRuleRefs.resetFields()
    },
    // 
    handleAddRoles() {
      this.$refs.RolesRuleRefs.validate(async (value) => {
        if (!value) return this.$message.error('')
        const { data: res } = await this.$http.post('roles', this.RolesRuleForm)
        if (res.meta.status !== 201) {
          return this.$message.error('')
        }
        this.getRolesList()
        this.$message.success(res.meta.msg)
        this.RolesDialogVisible = false
      })
    },
    // id
    async handleEdit(id) {
      this.editDialogVisible = true
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.RolesRuleFormEdit = res.data
      this.$message.success('')
    },
    // 
    handleCloseEdit() {
      this.$refs.RolesRuleRefsEdit.resetFields()
    },
    // 
    AddRoles() {
      var index = this.RolesList.findIndex(
        (v) => v.roleName === this.RolesRuleFormEdit.roleName
      )
      if (
        this.RolesRuleFormEdit.roleName === this.RolesList[index].roleName &&
        this.RolesRuleFormEdit.roleDesc === this.RolesList[index].roleDesc
      ) {
        return (this.editDialogVisible = false)
      } else {
        this.$refs.RolesRuleRefsEdit.validate(async (value) => {
          if (!value) return
          const { data: res } = await this.$http.put(
            'roles/' + this.RolesRuleFormEdit.roleId,
            {
              roleName: this.RolesRuleFormEdit.roleName,
              roleDesc: this.RolesRuleFormEdit.roleDesc
            }
          )
          if (res.meta.status !== 200) {
            return this.$message.error('')
          }
          this.$message.success(res.meta.msg)
          this.getRolesList()
          this.editDialogVisible = false
        })
      }
    },
    // 
    async handleDeleteRoles(id) {
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirm !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) return this.$message.error('')
      this.editDialogVisible = false
      this.getRolesList()
      this.$message.success('')
    },
    // id
    async removeById(role, rightId) {
      const confirm = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirm !== 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) return this.$message.error('')
      role.children = res.data
      this.$message.success('')
    },
    // 
    handleClosePower() {
      this.keysList = []
    },
    // 
    async handlePower(role) {
      this.roleId = role.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.treeList = res.data
      this.$message.success(res.meta.msg)
      this.dialogVisiblePower = true
      this.loopTree(role, this.keysList)
    },
    //  id
    loopTree(role, arr) {
      if (!role.children) return arr.push(role.id)
      role.children.forEach((v) => {
        this.loopTree(v, arr)
      })
    },
    // 
    async handleAddPower() {
      const PowerArr = [
        ...this.$refs.treeRefs.getCheckedKeys(),
        ...this.$refs.treeRefs.getHalfCheckedKeys()
      ]
      const strId = PowerArr.join(',')
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        {
          rids: strId
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.getRolesList()
      this.$message.success(res.meta.msg)
      this.dialogVisiblePower = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #ddd;
}
.bdbottom {
  border-bottom: 1px solid #ddd;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
