<template>
  <div id="rights">
    <!--  -->
    <Crumbs :crumbs="this.crumbs"></Crumbs>

    <!--  -->

    <el-card>
      <el-table :data="rightsList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="authName"></el-table-column>
        <el-table-column label="" prop="path"></el-table-column>
        <el-table-column label="" prop="level">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.level === '0'"></el-tag>
            <el-tag type="success" v-else-if="scope.row.level === '1'"></el-tag>
            <el-tag type="warning" v-else></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import Crumbs from '../crumbs.vue'
export default {
  data() {
    return {
      crumbs: [
        {
          name: '',
          id: 0
        },
        {
          name: '',
          id: 1
        }
      ],
      // 
      rightsList: []
    }
  },
  components: {
    Crumbs
  },
  created() {
    this.getRightsList()
  },
  methods: {
    // 
    async getRightsList() {
      const { data: res } = await this.$http.get('rights/list')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rightsList = res.data
      this.$message.success('')
    }
  }
}
</script>

<style lang="less" scoped>
</style>
