<template>
  <div id="login">
    <div class="login_detail">
      <!--  -->
      <div class="head_portrait">
        <img src="../../assets/logo.png" />
      </div>
      <!--  -->
      <el-form ref="form" :model="loginFrom" :rules="loginFromRules" class="login_from">
        <!--  -->
        <el-form-item prop="username">
          <el-input v-model="loginFrom.username" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="password">
          <el-input
            v-model="loginFrom.password"
            prefix-icon="iconfont icon-lock_fill"
            type="password"
          ></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item class="from_btn">
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginFrom"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      loginFrom: {
        username: 'admin',
        password: '123456'
      },
      // 
      loginFromRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 12,
            message: ' 6  12 ',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    // 
    resetLoginFrom() {
      console.log(this)
      this.$refs.form.resetFields()
    },
    // 
    login() {
      this.$refs.form.validate(async (valide) => {
        if (!valide) return
        const { data: res } = await this.$http.post('login', this.loginFrom)
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        // 1token sessionStorage 
        // 1.1APItoken
        // 1.2token  token  sessionStorage 
        window.sessionStorage.setItem('token', res.data.token)
        // 2/home
        this.$router.push('/home')
      })
    }
  }
}
</script>
<style lang="less" scoped>
#login {
  background-color: #2b4b6b;
  height: 100%;
  .login_detail {
    width: 550px;
    height: 400px;
    background-color: #fff;
    border-radius: 10px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    .head_portrait {
      width: 130px;
      height: 130px;
      border: 1px solid #eee;
      border-radius: 50%;
      padding: 10px;
      box-shadow: 0 0 10px #ddd;
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: #fff;
      img {
        border-radius: 50%;
        background-color: #eee;
      }
    }
    .login_from {
      position: absolute;
      bottom: 0;
      width: 100%;
      box-sizing: border-box;
      padding: 20px;
      .from_btn {
        display: flex;
        justify-content: flex-end;
      }
    }
  }
}
</style>
